import react, {useEffect, useState} from 'react';
import ArrowDownward from '@material-ui/icons/ArrowDownward';
import CircularProgress from '@material-ui/core/CircularProgress';
import DataTable from "react-data-table-component";
import {isEmpty} from 'lodash';

const DataTableComponent = (props) => {
  const [isLoading, setIsLoading] = useState(true);
  const [dataList, setDataList] = useState([]);
  const [selectedRowItem, setSelectedRowItem] = useState(null);
  const sortIcon = <ArrowDownward />;
  const progress = <CircularProgress size={70}/>;
  const customStyles = () => {
    return {
      rows: {
        style: {
          minHeight: '35px', // override the row height
        }
      },
      headCells: {
        style: {
          minHeight: '40px', // override the row height
          paddingLeft: '8px', // override the cell padding for head cells
          paddingRight: '8px',
          fontSize: '13px',
          fontWeight: 'bold',
          lineHeight: '30px'
        },
      },
      cells: {
        style: {
          paddingLeft: '3px', // override the cell padding for data cells
          paddingRight: '3px',
        },
      },
    };
  }
  // const darkTheme = () => {
  //   return {
  //     title: {
  //       fontSize: '22px',
  //       fontColor: '#FFFFFF',
  //       backgroundColor: '#363640',
  //     },
  //     contextMenu: {
  //       backgroundColor: '#E91E63',
  //       fontColor: '#FFFFFF',
  //     },
  //     header: {
  //       fontSize: '12px',
  //       fontColor: '#FFFFFF',
  //       backgroundColor: '#363640',
  //     },
  //     rows: {
  //       fontColor: '#FFFFFF',
  //       backgroundColor: '#363640',
  //       borderColor: 'rgba(255, 255, 255, .12)',
  //       hoverFontColor: 'black',
  //       hoverBackgroundColor: 'rgba(0, 0, 0, .24)',
  //     },
  //     cells: {
  //       cellPadding: '5px',
  //     },
  //   }
  // }
  useEffect(() => {
    setIsLoading(props.isLoading);
  });
  useEffect(() => {
    setDataList(props.dataList);
  });
  const selectedRowChange = (d) => {
    // if(d)
    setSelectedRowItem(d.selectedRows[0]);
    if(props.hasOwnProperty('onSelectedChagenItemCB')) {
      props.onSelectedChagenItemCB(d.selectedRows[0]);
    }
    console.log('selectedRowChange---', d);
  }
  return (
    <div>
    <DataTable
      columns={props.columns}
      data={dataList}
      noHeader={true}
      responsive={true}
      sortIcon={sortIcon}
      progressComponent={progress}
      // customTheme={this.darkTheme()}
      customStyles={customStyles()}
      defaultSortAsc={false}
      pagination
      paginationPerPage={15}
      highlightOnHover
      noDataComponent="  ."
      progressPending={isLoading}
      selectableRows={props.isSelectable}
      onSelectedRowsChange={selectedRowChange}
    />

    </div>
  );
}

export default DataTableComponent;
