import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import moment from 'moment';
import {isEmpty} from 'lodash';
import ThumbnailList from './ThumbnailList';
import CategorySeletctList from './CategorySeletctList';
import DataTableComponent from '../lib/dataDisplay/DataTableCompoenet';
class PostList extends Component {
  constructor(props) {
    console.log('props--', props);
    super(props);
    this.state  = {
      password: '', //  
      rePassword: '', //  
      pMessage:' .', //   (    )
      show: false,
      blindStyle: {
        width:'100%'
      },
      bodyEle: '',
      loading: true,
      dataList: [],
      groupType: '',
      columns: [],
      writeButton: '',
      isLoading: true,
      contentEl: '',

    };  //  state .    false .
  }
  componentDidMount() {
    this.setState({
      groupType: this.props.groupType,
      columns: this._getDataColumns()
    });

    this._getCateroeyList();
    this._setWriteButton();
    // this._getList();
  }
  componentDidUpdate() {
    if(this.state.groupType !== this.props.groupType) {
      this.setState({
        groupType: this.props.groupType,
      });
      this._setWriteButton();
      this._getCateroeyList();
    }
    console.log('componentDidUpdate--', this.props);

  }
  _setWriteButton() {
    if(isEmpty(this.props.userInfo)) {
      return;
    }
    let btnEl = ''
    if (this.props.groupType !== 'GUEST' && this.props.userInfo.grade === 'MASTER') {
      btnEl =  <button type="button" className="btn btn-dark btn-sm btn-fr mt-20">
              <Link to={{pathname:`${this.props.pathInfo.url}/detail`, search: `?groupType=${this.props.groupType}`, state: this.props.location.state, hash:'#write'}} className="text-white" style={{textDecoration: 'none'}}></Link>
              {/* <Link to={`${this.props.pathInfo.url}?groupType=${this.props.groupType}#write`} className="text-white" style={{textDecoration: 'none'}}></Link> */}
              </button>
    } else if (this.props.groupType === 'GUEST') {
      btnEl =  <button type="button" className="btn btn-dark btn-sm btn-fr mt-20">
              <Link to={{pathname:`${this.props.pathInfo.url}/detail`, search: `?groupType=${this.props.groupType}`, state: this.props.location.state, hash:'#write'}} className="text-white" style={{textDecoration: 'none'}}></Link>
              </button>
    }
    this.setState({
      writeButton: btnEl,
    })
  }
  _getDataColumns() {
    return [
      {
        name: "",
        selector: row => `${row.name}`,
        sortable: true,
        grow: 1,
      },
      {
        name: " ",
        selector: row => row.subject,
        sortable: true,
        compact: false,
        grow: 8,
        cell: d => <Link to={{pathname:`${this.props.pathInfo.url}/detail`, search: `?id=${d.id}&groupType=${this.props.groupType}`, state: this.props.location.state}} className="nav-link text-dark">
        <td>{d.subject} {d.comment_cnt  !== 0 && (`(${d.comment_cnt})`)}</td>
      </Link>
      },
      {
        name: "",
        selector: row => row.created_at,
        sortable: true,
        right: false,
        grow: 1,
        format: d => moment(d.created_at).format('YYYY.MM.DD'),
        // cell: d => <span>{moment(d.created_at).format('YYYY.MM.DD')}</span>,
      }
    ]
  }
  
  _getList = async() => {
    const res = await axios.get('/api/posts/list');
    console.log('_getList--', res)
    const bodyEle = this.boardList(res.data.result);
    this.setState({
      bodyEle: bodyEle,
      loading: false,
      dataList: res.data.result,
      // password: '',
    }) //       
  // this.setState({ hello : res.data.hello })
  }
  _getCateroeyList = async() => {
    const res = await axios.get(`/api/posts/category-list?groupType=${this.props.groupType}`);
    console.log('_getList--', res)
    // const bodyEle = this.boardList(res.data.result);
    this.setState({
      // bodyEle: bodyEle,
      loading: false,
      dataList: res.data.result,
      isLoading: false,
      // password: '',
    }) //       
    this._setContents();
  } 
  _setContents() {
    let contentEl;
    if(this.props.groupType === 'GALLERY') {
      contentEl = <ThumbnailList dataList={this.state.dataList} />
    } else {
      contentEl = <DataTableComponent columns={this.state.columns} dataList={this.state.dataList} isLoading={this.state.isLoading} isSelectable={false} />
    }
    this.setState({
      contentEl: contentEl
    })
  }
  boardList(list) {
    try{
      const listItems = list.map((item, index) =>
      <tr key={index}>
        {/* <td>{item.id}</td> */}
        <td>{item.name}</td>
        {/* <Nav.Link href={`${this.props.pathInfo.url}/detail#${item.id}`}>
          <td>{item.subject}</td>
        </Nav.Link> */}
        <Link to={{pathname: `${this.props.pathInfo.url}/detail`, search: `?id=${item.id}&groupType=${this.props.location.state.groupType}`, state: {groupType: this.props.location.state.groupType}}}
        className="nav-link text-dark">
          <td>{item.subject}</td>
        </Link>
        <td>{this.dateTimeFormat(item.created_at)}</td>
      </tr>
      );
      return (
        <tbody>{listItems}</tbody>
      );

    } catch(e) {
      console.log('boardList err', e);
    }
  } 
  dateTimeFormat(date) {
    return moment(date).format('YYYY.MM.DD');
  }
  // goDetail() {
  //   return href="#home"
  // } 
  handleConfirmPassword = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
    if (e.target.value !== '1234') {
      this.setState({
        pMessage: "  .",
        // password: '',
      }) //       
    } else {
      this.setState({
        blindStyle: {
          width:'0%'
        }
        // password: '',
      }) //       

    }
    console.log('password--', this.state.password);
  }

  render() {
    return (
      <div>
        {/* <Route path={`${this.props.pathInfo.url}/:type`} component={BoardDetail}/> */}
        <div className="clearfix content-btn-top mb-2">
         <CategorySeletctList location={this.props.location} categoryGroups={this.props.categoryGroups} isDisabled={true} groupType={this.props.groupType}/> 
         {this.state.writeButton}
          {/* <button type="button" className="btn btn-dark btn-sm btn-fr">
            <Link to={`${this.props.pathInfo.url}?groupType=${this.state.groupType}#write`} className="text-white" style={{textDecoration: 'none'}}></Link>
          </button> */}
        </div>
        <div className="card" style={{boxShadow: 'unset'}}>
          {this.state.contentEl}
          {/* <Table striped bordered hover size="sm">
            <colgroup>
              <col width="20%"></col>
              <col width="65%"></col>
              <col width="15%"></col>
            </colgroup>
            <thead>
              <tr>
                <th></th>
                <th> </th>
                <th></th>
              </tr>
            </thead>
            {this.state.bodyEle}
          </Table> */}
        </div>
      
      </div>
    );
  }
};

export default PostList;